 * {
     margin: 0;
     padding: 0;
     outline: 0;
     box-sizing: border-box;
 }

html, body, #root{
    height: 100%;
}

body {
    background: #f5f5f5;
}

body, input, button{
    font-family: Arial, Helvetica, sans-serif;
}

nav{
    width: 100%;
    height: 60px;
    background: rgba(174, 0, 255, 0.7);
    border-bottom: 1px solid rgba(174, 0, 255, 0.7);
}

label{
    margin: 0 40px 0 0;
    font-size: 40px;
    line-height: 55px;    
    display: none;
    width: 30px;
    float: right;
}

#toggle{
    display: none;
}

nav ul{    
    display: flex;
    margin: 0 30px 0 0;
    padding: 0;
    float: right;
    position: sticky;    
    list-style: none;    
}

nav ul li{
    text-decoration: none;
    margin: 0 10px;
}

nav ul li a{
    display: block;
    color: #fff;
    padding: 12px 40px;
    text-decoration: none;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 20px;
    line-height: 40px;   
}


nav ul li a:hover{
    color: black;
}

nav a img{
    width: 130px;
    height: 55px;
    color: #fff;
    float: left;
    margin-left: 3%
}

@media only screen and (max-width: 1100px){
    label {
        display: block;
        cursor: pointer;
    }
    ul{
        text-align: center;
        width: 100%;  
        float: none;
    }
    ul li a{
        border-bottom: 1px solid rgba(174, 0, 255, 0.7);
        margin: 0;
    }
    #toggle:not(:checked) ~ ul li{
        display: none;
    }
    #toggle:checked ~ ul{
        display: block;
    }
}


.titleName{
    text-align: center;
    font-size: 32px;
    margin: 10px;
}